// accordion
try {
  const accordions = document.querySelectorAll('.faq__item');

  accordions.forEach((el) => {
    if (el.classList.contains('faq__item--open')) {
      const elControl = el.querySelector('.js_accordion-control');
      const elContent = el.querySelector('.js_accordion-content');
      elControl.setAttribute('aria-expanded', true);
      elContent.setAttribute('aria-hidden', false);
      elContent.style.maxHeight = elContent.scrollHeight + 'px';
    }
    el.addEventListener('click', (e) => {
      const self = e.currentTarget;
      const control = self.querySelector('.js_accordion-control');
      const content = self.querySelector('.js_accordion-content');
      if (e.target === control || control.contains(e.target)) {
        if (self.classList.contains('faq__item--open')) {
          self.classList.remove('faq__item--open');
          control.setAttribute('aria-expanded', false);
          content.setAttribute('aria-hidden', true);
          content.style.maxHeight = null;
        } else {
          self.classList.add('faq__item--open');
          control.setAttribute('aria-expanded', true);
          content.setAttribute('aria-hidden', false);
          content.style.maxHeight = content.scrollHeight + 'px';
        }
      }
    });
  });
} catch (e) {}

const mapWrapper = document.querySelector('.js-map-wrapper');
const mapLoadingSpinner = document.querySelector('.js-map-loading-spinner');

function changeMap() {
  const mapScript = document.createElement('script');
  mapScript.setAttribute('type', 'text/javascript');
  mapScript.setAttribute('async', 'async');
  mapScript.setAttribute('charset', 'utf-8');
  mapScript.setAttribute(
    'src',
    'https://api-maps.yandex.ru/services/constructor/1.0/js/?um=constructor%3A70fd1b751261467594627eef98d12191367d06f504ac3916fe445a75c29436e9&amp;lang=ru_RU&amp;scroll=true',
  );

  try {
    const ymap = mapWrapper.querySelector('ymaps');
    ymap.remove();
    mapLoadingSpinner.style.display = 'block';
    // eslint-disable-next-line no-empty
  } catch (e) {}
  mapWrapper.appendChild(mapScript);
  if ('MutationObserver' in window) {
    const ymapsObserver = new MutationObserver(() => {
      if (mapWrapper.querySelector('ymaps')) {
        mapLoadingSpinner.style.display = 'none';
        ymapsObserver.disconnect();
      }
    });

    ymapsObserver.observe(mapWrapper, {
      attributes: false,
      childList: true,
      characterData: false,
      subtree: true,
    });
  } else {
    mapScript.onload = () => {
      mapLoadingSpinner.style.display = 'none';
    };
  }
}

try {
  if ('IntersectionObserver' in window) {
    const mapObserver = new IntersectionObserver((elements) => {
      elements.forEach((el) => {
        if (el.isIntersecting) {
          const target = el.target;
          changeMap();
          mapObserver.unobserve(target);
        }
      });
    });

    mapObserver.observe(mapWrapper);
  } else {
    changeMap();
  }
} catch (e) {}

